import React from 'react';
import "antd/dist/antd.css";
import "./style.css";
import { Col, Row } from 'antd';

const LeftMenu = () => {
    return(
        <div className="left-menu">
            <Row>
                <Col span={6}>
                    <h1>Left Menu</h1>
                </Col>
            </Row>
        </div>
    );
}

export default LeftMenu;